.catalog-menu{
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background-color: #fff;
  z-index: 22;
  padding: 2.5em 0;
  border-top: $border2;
  border-bottom: $border2;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &__inner{
    display: grid;
    grid-template-columns: 20.4375em calc(100% - 20.4375em - 3.125em);
    gap: 3.125em;
  }
  &__bg,
  &__close{
    display: none;
  }
}

.catalog-categories{
  border: $border2;
  border-radius: .1875em;
  overflow: hidden;
  &__title{
    display: none;
  }
  &__item{
    padding: 1.06em 1.875em;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 500;
    border-bottom: $border2;
    transition: all .3s;
    cursor: pointer;
    &:last-child{
      border-bottom: none;
    }
    span{
      font-size: 1em;
      line-height: 140%;
    }
    svg{
      width: 1.125em;
      height: auto;
      opacity: .5;
      margin-left: .25em;
      margin-top: .125em;
    }
    &:hover,
    &._active{
      color: #fff;
      background-color: $primary;
      border-color: $primary;
      svg{
        opacity: 1;
      }
      path{
        stroke: #fff;
      }
    }
    &._active{
      pointer-events: none;
    }
  }
}

.catalog-submenu{
  max-width: 23.25em;
  display: none;
  &._active{
    display: block;
  }
  &__back{
    display: none;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: .94em 0;
  }
  &__link{
    @include lastM(0 0 .81em);
    span{
      font-size: 1.0625em;
      line-height: 153%;
    }
  }
}

._open-catalog-menu{
  .catalog-menu{
    opacity: 1;
    pointer-events: unset;
  }
}


@media screen and (max-width: 992px){
  .catalog-menu{
    padding: 0;
    .container{
      padding: 0;
    }
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
    @media screen and (min-width: 577px){
      @include menu;
    }
    &._open-submenu{
      .catalog-categories__title,
      .catalog-categories{
        display: none;
      }
      .catalog-submenu{
        &._active{
          display: block;
        }
      }
    }
  }

  .catalog-categories{
    border-left: none;
    border-right: none;
    border-radius: 0;
    &__item{
      padding: 1.56em;
      span{
        font-size: .9375em;
      }
    }
    &__title{
      display: flex;
      align-items: center;
      padding: 1.56em;
      font-weight: 700;
      svg{
        display: none;
      }
    }
  }

  .catalog-submenu{
    display: none;
    &__back{
      display: flex;
      align-items: flex-start;
      padding: 1.56em;
      font-weight: 700;
      cursor: pointer;
      svg{
        width: .75em;
        height: auto;
        margin-right: 1em;
      }
      span{
        font-size: 1.0625em;
        line-height: 129%;
      }
    }
    &__links{
      border-top: $border2;
      padding: 0;
    }
    &__link{
      display: flex;
      padding: 1.56em;
      width: 100%;
      margin: 0 !important;
      border-bottom: $border2;
    }
  }

  ._open-catalog-menu{
    .main-slider{
      pointer-events: none;
    }
    .catalog-menu{
      transform: translateX(0);
      &__bg{
        display: block;
        //pointer-events: none;
      }
      &__close{
        display: flex;
        //pointer-events: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-menu{
    display: none;
    opacity: 1;
    pointer-events: unset;
    position: static;
  }

  .catalog-categories{
    &__item{
      padding: .94em;
    }
    &__title{
      padding: .94em;
      svg{
        display: block;
        width: .4375em;
        margin-right: .5em;
      }
      .text17{
        font-size: .9375em;
      }
    }
  }

  .catalog-submenu{
    display: none;
    &__back,
    &__link{
      padding: .94em;
      span{
        font-size: .9375em;
      }
      svg{
        margin-right: .4375em;
      }
    }
  }

  ._open-catalog-menu{
    .menu{
      display: none;
    }
    .catalog-menu{
      display: block;
    }
  }
}
